<template>
  <div class="login-container">
    <div class="form-wrap">
      <van-icon class="cancel-btn" name="close" @click="$router.back()"/>
      <van-tabs background="rgba(255, 255, 255m .8)" animated v-model="active">
        <van-tab title="" name="login">
          <!--  -->
          <van-form ref="form">
            <van-field
              v-model="mobile"
              maxlength="11"
              clearable
              name=""
              label=""
              placeholder=""
            />
            <van-field
              v-model="code"
              name=""
              label=""
              clearable
              placeholder=""
            >
              <!--  -  -->
              <van-button
                v-if="showSendBtn"
                slot="button"
                type="primary"
                size="mini"
                :disabled="codeDisabled"
                @click="hSendCode"
              ></van-button>
              <!--  -  -->
              <van-button
                v-else
                slot="button"
                type="primary"
                size="small"
                disabled
              >{{countdown}}s</van-button>
            </van-field>
            <div style="margin: 16px;">
              <van-button round block type="info" @click="hLogin"></van-button>
            </div>
          </van-form>
        </van-tab>
        <!--  -->
        <van-tab title="" name="register">
          <van-form ref="form">
            <van-field
              v-model="register_mobile"
              maxlength="11"
              clearable
              name=""
              label=""
              placeholder=""
            />
            <van-field
              v-model="register_pwd"
              name=""
              label=""
              clearable
              placeholder=""
            />
            <div style="margin: 19px 16px 18px;">
              <van-button round block type="info" @click="hRegister"></van-button>
            </div>
          </van-form>
        </van-tab>
    </van-tabs>
    <!--  -->
      <van-divider :style="{ color: '#1989fa', borderColor: '#1989fa', padding: '0 16px' }"></van-divider>
      <!--  QQ  -->
      <div class="svg-wrap">
        <div class="svg-item" @click="hOtherLogin('wechat')">
          <svg-icon iconClass="wechat"></svg-icon>
          <span class="svg-txt"></span>
        </div>
        <div class="svg-item" @click="hOtherLogin('QQ')">
          <svg-icon iconClass="QQ"></svg-icon>
          <span class="svg-txt">QQ</span>
        </div>
      </div>
      <!--  -->
      <p class="agreement">
        
        <br>
        <a href="#"></a><a href="#"></a>
      </p>
    </div>
  </div>
</template>

<script>
import { Tab, Tabs, Form, Button, Field, Dialog, Toast, Divider, Icon } from 'vant'
import { getCode, login } from '@/api/login.js'
import { mapMutations } from 'vuex'
export default {
  components: {
    [Icon.name]: Icon,
    [Divider.name]: Divider,
    [Toast.name]: Toast,
    [Dialog.name]: Dialog,
    [Field.name]: Field,
    [Button.name]: Button,
    [Form.name]: Form,
    [Tab.name]: Tab,
    [Tabs.name]: Tabs
  },
  computed: {
    // 
    mobileValid () {
      const value = this.active === 'login' ? this.mobile : this.register_mobile
      if (value.length > 10) {
        return /^1[3-8]\d{9}$/.test(value)
      } else {
        return false
      }
    },
    // 
    codeDisabled () {
      if (this.mobile.length > 10 && this.mobileValid) {
        return false
      } else {
        return true
      }
    }
  },
  data () {
    return {
      register_mobile: '', // 
      register_pwd: '', // 
      active: 'login', //  / 
      pattern: /^1[3-8]\d{9}$/, // 
      countdown: 60, // 
      showSendBtn: true, //  (true) / (false) 
      mobile: '', // 
      code: '' // 
    }
  },
  methods: {
    ...mapMutations(['updateUserInfo']),
    // 
    async hRegister () {
      if (this.register_mobile.length < 1) {
        Toast({
          message: '',
          duration: 800
        })
        return
      } else if (!this.mobileValid) {
        Toast({
          message: '',
          duration: 800
        })
        return
      } else if (this.register_pwd.length < 6 || this.register_pwd.length > 12) {
        Toast({
          message: '6-12',
          duration: 800
        })
        return
      }

      const res = await login(this.register_mobile, this.register_pwd)
      if (res.success_code === 200) {
        this.updateUserInfo(res.data)
        this.$route.query.redirect ? this.$router.replace(this.$route.query.redirect) : this.$router.back()
      } else {
        Toast({
          message: '',
          duration: 1000
        })
      }
    },
    // 
    hOtherLogin (type) {
      if (type === 'wechat') {
        Toast({
          message: '',
          duration: 800
        })
      } else if (type === 'QQ') {
        Toast({
          message: 'QQ',
          duration: 800
        })
      }
    },
    // 
    async hLogin () {
      if (this.mobile.length < 11 || !this.mobileValid) {
        Toast({
          message: '',
          duration: 800
        })
        return
      } else if (this.code.length !== 6) {
        Toast({
          message: '',
          duration: 800
        })
        return
      }
      const res = await login(this.mobile, this.code)
      this.updateUserInfo(res.data)
      this.$route.query.redirect ? this.$router.replace(this.$route.query.redirect) : this.$router.back()
    },
    async hSendCode () {
      //   /  
      this.showSendBtn = !this.showSendBtn

      // 
      const t = setInterval(() => {
        if (this.countdown === 1) {
          clearInterval(t)
          this.showSendBtn = true
          this.countdown = 60
        }
        this.countdown--
      }, 1000)

      // 
      const res = await getCode(this.mobile)
      // 
      if (res.success_code === 200) {
        Dialog.alert({
          title: '',
          message: res.data.code
        }).then(() => {
          // on close
          // 
          this.code = res.data.code
        })
      }
    }
  }
}
</script>

<style lang="less" scoped>
.login-container {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: url('~@/assets/images/login/login-bg.jpg') no-repeat;
  display: flex;
  align-items: center;
  justify-content: center;

  /deep/.van-tabs__nav {
    background-color: rgba(255, 255, 255, 0);
  }

  .form-wrap {
    position: relative;
    width: 9.013rem;
    max-width: 90%;
    // height: 13.333rem;
    // max-height: 80%;
    padding: .933rem;
    background-color: rgba(255, 255, 255, .8);
    box-sizing: border-box;
    border-radius: .25rem;
    .cancel-btn {
      position: absolute;
      right: .4rem;
      top: .4rem;
    }
  }

  .svg-wrap {
    margin-top: .8rem;
    display: flex;
    justify-content: space-around;
    .svg-item {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
      .svg-txt {
        font-size: .32rem;
        margin-top: .267rem;
      }
    }
  }

  .agreement {
    margin-top: .533rem;
    font-size: 12px;
  }

}

</style>

<style lang="less">
.van-toast {
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  margin: auto;
  height: .8rem;
  transform: translate3d(0, 0, 0);
}

.van-dialog {
  width: 8.347rem;
  height: 4rem;
  position: fixed;
  left:0;
  right:0;
  top:0;
  bottom:0;
  margin:auto;
  transform: translate3d(0,0,0);

  .van-button__text {
    color: #1989fa;
  }
}
</style>
